//
//Full calendar scss start
//

.fc {
    .fc-toolbar{
        flex-direction:column;
        .btn-group{
            gap: .5rem;
        }
    }
    .fc-list-empty{
        @include border-radius();
        background-color: var(--#{$prefix}tertiary-bg);
    }
    .fc-daygrid-body-natural .fc-daygrid-day-events{
        margin-top: .25rem;
        margin-bottom: .25rem;
    }
    .fc-col-header-cell-cushion{
        text-transform: uppercase;
        font-size: $font-size-sm;
        font-weight: $headings-font-weight;
        color: var(--#{$prefix}tertiary-color);
    }
    td, th {
        vertical-align: top;
        padding: 0;
        border: 1px solid var(--#{$prefix}border-color);
        background-color: $card-bg;
    }
    th{
        border-bottom: 0px;
    }
    .fc-scrollgrid table {
        border-top-style: inherit;
        border-left-style: inherit;
    }
    .fc-list-event:hover td{
        background-color: var(--#{$prefix}body-bg);
    }
    .fc-daygrid-dot-event .fc-event-title{
        text-overflow: ellipsis;
    }
    .table-bordered>:not(caption)>*{
        border: 0px;
    }
    .fc-list-table tr>* {
         border-left: 1px;
         border-right: 1px;
    }
    .fc-list-day-cushion,.fc-list-day-cushion{
        padding: $card-spacer-y $card-spacer-x;
    }
    .fc-direction-ltr .fc-list-table .fc-list-event-graphic {
        padding-right: $card-spacer-x;
    }
    .fc-daygrid-event {
        padding: $input-btn-padding-y-sm * .5 $input-btn-padding-x-sm;
        border-radius: 0;
        @include border-radius();
    }
    .fc-daygrid-dot-event.fc-event-mirror, .fc-daygrid-dot-event:hover {
        background:var(--#{$prefix}secondary-bg);
    }
    .fc-daygrid-day.fc-day-today{
        background-color:var(--#{$prefix}body-bg);
    }
    .fc-toolbar-chunk+.fc-toolbar-chunk{
        margin-top:$spacer * .75;
    }
    .btn{
        padding: $input-btn-padding-y-sm $input-btn-padding-x-sm;
        font-size: $font-size-sm;
    }
    .fc-toolbar-chunk .btn{
        border-radius: 0;
        @include border-radius();
    }
    .fc-toolbar-chunk .btn-group{
       .btn{
        border-radius: 0;
        @include border-radius();
       }
    }
}
.fc .fc-list-sticky .fc-list-day>*{
    background-color: var(--#{$prefix}body-bg);
}
@include media-breakpoint-up(md){
    .fc{
        .fc-toolbar{
            flex-direction:row;
        }
        .fc-toolbar-chunk+.fc-toolbar-chunk{
            margin: 0;
        }
    }
    }