//
//Dark and light mode style switcher style start
//
[data-bs-theme=dark]{
  .theme-icon-active i{
    &::before{
      content: "\F495";
    }
  }
}
[data-bs-theme=auto]{
  .theme-icon-active i{
    &::before{
      content: "\F288";
    }
  }
}
[data-bs-theme=light]{
  .theme-icon-active i{
    &::before{
      content: "\F1D3";
    }
  }
}