//Apex charts custom css
.apexcharts-tooltip{
    box-shadow: $box-shadow-sm!important;
}

.apexcharts-xaxis text, .apexcharts-yaxis text {
    fill:var(--#{$prefix}tertiary-color);
    font-weight: $font-weight-base;
}
.apexcharts-gridline{
    stroke: var(--#{$prefix}border-color);
}
.apexcharts-tooltip.apexcharts-theme-light {
    border:0px!important;
    border-radius: 0px;
    border-radius: $card-border-radius;
    color: var(--#{$prefix}body-color);
    background: var(--#{$prefix}tertiary-bg)!important;
}
.apexcharts-tooltip.apexcharts-theme-light .apexcharts-tooltip-title{
    background: var(--#{$prefix}primary)!important;
    border-top-left-radius: $card-border-radius;
    border-top-right-radius: $card-border-radius;
    border-bottom: 0px!important;
    color: var(--#{$prefix}white);
    padding-left: $spacer * .75;
}
.apexcharts-tooltip-text-y-value, .apexcharts-tooltip-text-goals-value, .apexcharts-tooltip-text-z-value{
    font-weight: $font-weight-bold!important;
}

.apexcharts-xaxistooltip{
    background-color:var(--#{$prefix}body-bg)!important;
    border: 1px solid var(--#{$prefix}border-color)!important;
    color: var(--#{$prefix}body-color);
}
.apexcharts-xaxistooltip-bottom:before {
   border-bottom-color: var(--#{$prefix}border-color)!important;
}
.apexcharts-xaxistooltip-bottom:after {
    border-bottom-color: var(--#{$prefix}border-color)!important;
}
.apexcharts-xaxistooltip-text{
color: $text-muted!important;
}