//Kanban

.task-list-items {
    min-height: 100px;
    position: relative;
    .card {
        white-space: normal;
    }

    &:before {
        content: "No Tasks";
        position: absolute;
        line-height: 110px;
        width: 100%;
        padding-left: 1.25rem;
        display: block;
        text-align: center;
        font-weight: $headings-font-weight;
        color: var(--#{$prefix}tertiary-color);
        border: 1px dashed var(--#{$prefix}border-color);
        border-radius: $border-radius-lg;
    }
}